import styled from "styled-components";

export const Wrapperstyle = styled.div`
  width: 100%;
  min-height: 100vh;
  background-color: #333;
  display: flex;
  justify-content: center;
  align-items: center;
  /* background-image: url("data:image/jpeg;base64,");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; */
  font-family: "Comic Neue", "cursive", sans-serif;
  background: rgb(0, 158, 226);
  background: linear-gradient(
    58deg,
    rgba(0, 158, 226, 1) 1%,
    rgba(0, 158, 239, 1) 46%,
    rgba(1, 34, 156, 1) 100%
  );

  &::before {
    content: "";
    position: absolute;
    top: 15px;
    left: 105px;
    width: 200px;
    height: 200px;
    background-color: #ffbf2b;
    box-shadow: 0 0 20px #ffbf2b, 0 0 80px #ffbf2b, 0 0 180px #ffbf2b;
    border-radius: 50%;
  }
  &::after {
    content: "";
    position: absolute;
    top: 200px;
    right: 482px;
    width: 120px;
    height: 120px;
    background-color: #eee;
    border-radius: 40% 4px 4px 80%;
  }

  h2 {
    text-align: center;
    font-size: 30px;
    margin: 5px;
    p {
      margin: 5px;
      margin-bottom: 15px;
      font-size: 22px;
      color: #444;
    }
  }
`;

export const Boxstyle = styled.div`
  width: fit-content;
  min-width: 1400px;
  height: fit-content;
  background-color: #333;
  padding: 20px 30px 35px 20px;
  margin: auto auto auto 80px;
  margin: auto;
  border: 2px solid #ccc;
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255, 0.7);
  backdrop-filter: blur(9px);
  border-radius: 15px;
  z-index: 999;
  input {
    width: 60%;
    height: 40px;
    margin: 15px auto;
    border: 2px solid lightgrey;
    background-color: #eee;
    border-radius: 5px;
    font-size: 17px;
    text-indent: 10px;
    transition-duration: 0.4s;
    text-transform: capitalize;
    &:hover {
      background-color: #fff;
      color: #111;
    }
  }
`;

export const FlexBox = styled.div`
  width: 100%;
  margin: auto;
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
`;

export const ImgBox = styled.div`
  width: 32%;
  height: fit-content;
  display: flex;
  flex-direction: column;
  img {
    width: 340px;
    margin: -100px 0;
    margin-bottom: -100px;
  }
  h3 {
    font-size: 35px;
    font-weight: bold;
    text-align: left;
    letter-spacing: 4px;
    margin-top: 80px;
    margin-bottom: 0;
    .h2 {
      font-size: 24px;
      font-weight: bold;
      letter-spacing: 6px;
      color: #444;
      margin: 5px;
      margin-right: 10px;
      text-align: right;
    }
  }
`;

export const DisplayFlex = styled.div`
  display: flex;
  justify-content: space-between;
  width: 68%;
`;

export const TextsBox = styled.div`
  width: 55%;
  display: flex;
  flex-direction: column;
  font-family: "Bebas Neue", "cursive", sans-serif;
`;

export const MapBox = styled.div`
  width: 40%;
  margin-left: auto;
`;

export const TempText = styled.h2`
  font-size: 30px;
  text-align: center;
  color: #777;
  font-family: "Underdog", cursive;

  span {
    color: #ffbf2b;
  }
`;

export const FeelsLike = styled.div`
  width: 100%;
  height: fit-content;
  padding: 8px 4px;
  border: 1px solid #aaa;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-family: "Underdog", cursive;
  margin-bottom: 13px;
  div {
    display: flex;
    align-items: center;
    img {
      width: 28px !important;
      margin-right: 7px;
    }
    span {
      font-size: 13px;
      font-weight: 600;
      text-transform: capitalize;
      color: #333;
    }
  }
`;

export const FeelsLikeSmall = styled.div`
  width: 49%;
  height: fit-content;
  padding: 8px 4px;
  border: 1px solid #aaa;
  display: flex;
  justify-content: space-between;
  align-items: center;
`;

export const Mapstyle = styled.div`
  padding: 0;
  .mapouter {
    position: relative;
    text-align: right;
    width: 100%;
    height: 200px;
  }
  .gmap_canvas {
    overflow: hidden;
    background: none !important;
    width: 100%;
    height: 200px;
    border: 1px solid #aaa;
    padding: 2px;
  }
  .gmap_iframe {
    height: 200px !important;
  }
`;
